'use client';

import { useState, useEffect } from 'react';
import { NamespaceMethod } from '../../types/Namespace';
import { FiPlus, FiTrash2 } from 'react-icons/fi';

interface MethodModalProps {
  isOpen: boolean;
  onClose: () => void;
  onSave: (data: Omit<NamespaceMethod, 'namespace-account-method-id'>) => Promise<void>;
  namespaceId: string;
  initialData?: NamespaceMethod;
}

export default function MethodModal({
  isOpen,
  onClose,
  onSave,
  namespaceId,
  initialData
}: MethodModalProps) {
  const [formData, setFormData] = useState({
    'namespace-id': namespaceId,
    'namespace-account-method-name': '',
    'namespace-account-method-url-override': '',
    'namespace-account-method-queryParams': [{ key: '', value: '' }]
  });

  // Reset form when modal opens/closes or initialData changes
  useEffect(() => {
    if (isOpen) {
      setFormData({
        'namespace-id': namespaceId,
        'namespace-account-method-name': initialData?.['namespace-account-method-name'] || '',
        'namespace-account-method-url-override': initialData?.['namespace-account-method-url-override'] || '',
        'namespace-account-method-queryParams': initialData?.['namespace-account-method-queryParams']?.length 
          ? initialData['namespace-account-method-queryParams']
          : [{ key: '', value: '' }]
      });
    }
  }, [isOpen, initialData, namespaceId]);

  if (!isOpen) return null;

  const addParam = () => {
    setFormData(prev => ({
      ...prev,
      'namespace-account-method-queryParams': [
        ...prev['namespace-account-method-queryParams'],
        { key: '', value: '' }
      ]
    }));
  };

  const removeParam = (index: number) => {
    setFormData(prev => ({
      ...prev,
      'namespace-account-method-queryParams': prev['namespace-account-method-queryParams'].filter((_, i) => i !== index)
    }));
  };

  const updateParam = (index: number, field: 'key' | 'value', value: string) => {
    setFormData(prev => ({
      ...prev,
      'namespace-account-method-queryParams': prev['namespace-account-method-queryParams'].map((param, i) => 
        i === index ? { ...param, [field]: value } : param
      )
    }));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    await onSave(formData);
  };

  return (
    <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50">
      <form onSubmit={handleSubmit} className="bg-white rounded-lg p-6 w-[600px]">
        <h2 className="text-lg font-medium mb-4">
          {initialData ? 'Edit Method' : 'Create Method'}
        </h2>
        
        <div className="space-y-4">
          <div>
            <label className="block text-sm font-medium mb-1">Method Name</label>
            <input
              type="text"
              required
              value={formData['namespace-account-method-name']}
              onChange={(e) => setFormData(prev => ({
                ...prev,
                'namespace-account-method-name': e.target.value
              }))}
              className="w-full px-3 py-2 border rounded"
            />
          </div>

          <div>
            <label className="block text-sm font-medium mb-1">URL Override</label>
            <input
              type="text"
              value={formData['namespace-account-method-url-override']}
              onChange={(e) => setFormData(prev => ({
                ...prev,
                'namespace-account-method-url-override': e.target.value
              }))}
              className="w-full px-3 py-2 border rounded"
            />
          </div>

          <div>
            <div className="flex justify-between items-center mb-2">
              <label className="block text-sm font-medium">Query Parameters</label>
              <button
                type="button"
                onClick={addParam}
                className="text-[#ff6b4a] hover:text-[#ff5436] text-sm flex items-center gap-1"
              >
                <FiPlus /> Add Parameter
              </button>
            </div>
            
            <div className="space-y-2">
              {formData['namespace-account-method-queryParams'].map((param, index) => (
                <div key={index} className="flex gap-2">
                  <input
                    type="text"
                    value={param.key}
                    onChange={(e) => updateParam(index, 'key', e.target.value)}
                    placeholder="Key"
                    className="flex-1 px-3 py-2 border rounded"
                  />
                  <input
                    type="text"
                    value={param.value}
                    onChange={(e) => updateParam(index, 'value', e.target.value)}
                    placeholder="Value"
                    className="flex-1 px-3 py-2 border rounded"
                  />
                  <button
                    type="button"
                    onClick={() => removeParam(index)}
                    className="text-red-500 hover:text-red-600"
                  >
                    <FiTrash2 />
                  </button>
                </div>
              ))}
            </div>
          </div>

          <div className="flex justify-end gap-2 mt-6">
            <button
              type="button"
              onClick={onClose}
              className="px-4 py-2 text-gray-600 hover:text-gray-800"
            >
              Cancel
            </button>
            <button
              type="submit"
              className="px-4 py-2 bg-[#ff6b4a] text-white rounded hover:bg-[#ff5436]"
            >
              Save
            </button>
          </div>
        </div>
      </form>
    </div>
  );
} 